import Head from "next/head";
import Image from "next/image";
import Link from "next/link";
import styles from "../stylesogin.module.css";

export default function Login() {
  return (
    <>
      <Head>
        <title>Login - EasyControl</title>
        <meta name="description" content="Tela de login do sistema EasyControl" />
      </Head>

      <div className={styles.page}>
        <section className={styles.formSection}>
          <div className={styles.logoContainer}>
            <Image src="/logoEasyControl.jpg" alt="Logo EasyControl" width={120} height={120} />
            <h1 className={styles.title}>EasyControl</h1>
            <p className={styles.subtitle}>Controle sobre veículos e rotas</p>
          </div>

          <form className={styles.form} onSubmit={(e) => e.preventDefault()}>
            <label htmlFor="email">E-mail</label>
            <input
              id="email"
              type="email"
              placeholder="seu@exemplo.com"
              aria-label="Email"
              required
            />

            <label htmlFor="senha">Senha</label>
            <input
              id="senha"
              type="password"
              placeholder="••••••••"
              aria-label="Senha"
              required
            />

            <button type="submit" className={styles.btnPrimary}>
              Entrar
            </button>
          </form>

          <div className={styles.linksColumn}>
            <Link href="/redefinir-senha" className={styles.link}>
              Esqueci minha senha/código
            </Link>
            <Link href="/criar-conta" className={styles.linkSecondary}>
              Criar conta
            </Link>
          </div>
        </section>

        <aside className={styles.imageSection} />
      </div>
    </>
  );
}
